@media (min-width: 576px) {
  .velo-mini {
    background: #f4f4f4;
    display: flex;
    justify-content: center;
    padding-top: 92px;
    padding-bottom: 88px;
    position: relative;
    z-index: -2;
  }
  
  .velo-mini-title {
    color: #10145B;
    width: 328px;
    margin-right: 230px;
  }
  
  .velo-mini__text {
    width: 477px;
    font-weight: 400;
    font-size: 23px;
    line-height: 28px;
    transform: translate(0, 300px);
  }
  
  .velo-mini__text strong {
    text-transform: uppercase;
    font-weight: 900;
  }

  .velo-mini-background {
    position: absolute;
    background: #10145b;
    left: 0;
    bottom: -1px;
    clip-path: polygon(68% 100%, 0 38%, 0 100%);
    height: 108%;
    width: 43%;
  }

  .velo-mini__medium {
    background: #10145b;
    display: flex;
    justify-content: center;
    padding-top: 79px;
    padding-bottom: 200px;
    position: relative;
  }

  .velo-mini__medium-title {
    font-weight: 700;
    font-size: 48px;
    line-height: 48px;
    letter-spacing: -1px;
    text-transform: uppercase;
    color: #2cd5c4;
    width: 450px;
  }

  .velo-mini__medium_text {
    font-weight: 400;
    font-size: 23px;
    line-height: 28px;
    color: #fff;
    margin-top: 31px;
  }

  .velo-mint__triangle {
    position: absolute;
    top: -175px;
    left: -100%;
    transition: 1s;
  }

  .velo-mint__big-jar {
    position: absolute;
    top: 0;
    z-index: -1;
    transition: 1s;
  }
  
  .velo-jar-background {
    background-color: #10145b;
    width: 480px;
    height: 172px;
    transform: rotate(-24deg);
    position: absolute;
    bottom: 49px;
    left: 12%;
  }

  .velo-mint__small-jar {
    position: absolute;
    top: -30px;
    left: -20%;
    transform: rotate(-90deg);
    transition: 1s;
  }

  .velo-mini__img {
    width: 328px;
    margin-right: 200px;
  }

  .velo-mini-berry {
    background: #4d49be;
    display: flex;
    justify-content: center;
    padding-top: 79px;
    padding-bottom: 155px;
    position: relative;
  }

  .velo-mini__berry-title {
    width: 450px;
    color: #a5b0e3;
  }

  .berry-background-triangle {
    background: #4d49be;
    clip-path: polygon(69% 100%, 0 47%, 0 100%);
  }

  .berry-jar-background {
    background: #4d49be;
  }


}

.taste {
  font-weight: normal;
  font-size: 23px;
  line-height: 28px;
  color: #000;
}

.new-tast__img {
  position: relative;
  right: -2000px;
}

.streight {
  font-weight: normal;
  font-size: 23px;
  line-height: 28px;
  color: #000;
}

.product-taste {
  text-transform: uppercase;
}

.new-taste__item:first-child {
  margin-right: 100px;
}

@media (max-width: 1100px) {
  .new-taste__item:first-child {
    margin-right: 0;
  }
}

@media (max-width: 992px) {
  #new-taste {
    padding-top: 0;
  }

  #new-taste::before {
    content: '';
    width: 100%;
    height: 100px;
    background: #4d81ba;
    display: block;
    margin-bottom: 20px;
    clip-path: polygon(100% 0, 100% 100%, 50% 33%, 0 100%, 0 0);
  }

  .title-top {
    font-size: 36px;
  }

  .jar-subtitle {
    font-size: 32px;
  }
}

@media (max-width: 768px) {
  .title-top {
    font-size: 27px;
  }
}

@media (max-width: 660px) {
  .new-taste {
    flex-wrap: wrap;
  }

  .new-taste__item {
    flex-direction: row;
    align-items: center;
  }

  .new-taste__item:last-child {
    order: -1;
  }
}

@media (max-width: 576px) {
  .subtitle-top {
    font-weight: bold;
    font-size: 18px;
    line-height: 25px;
  }

  .title-top {
    font-size: 20px;
    line-height: 26px;
  }
}

@media (max-width: 1666px) {
  .velo-mint__triangle {
    top: -140px;
  }

  .velo-mint__triangle img {
    width: 93%;
  }
}

@media (max-width: 1536px) {
  .velo-mint__big-jar img {
    width: 80%;
  }

  .velo-mint__triangle img {
    width: 80%;
  }

  .velo-mint__small-jar img {
    width: 80%;
  }

  .velo-jar-background {
    width: 337px;
    height: 126px;
  }

  .velo-mini-background {
    height: 87%;
  }
}

@media (max-width: 1400px) {
  .velo-mint__big-jar img {
    width: 70%;
  }

  .velo-mint__triangle img {
    width: 70%;
  }

  .velo-mint__small-jar img {
    width: 70%;
  }

  .velo-jar-background {
    width: 341px;
    height: 85px;
  }

  .title {
    font-size: 35px;
    line-height: 35px;
  }

  .velo-mint__triangle {
    top: -84px;
  }

  .velo-mini-background {
    height: 65%;
  }

  .velo-mint__small-jar {
    top: 9px
  }

  .velo-mini-title {
    width: 255px;
  }

  .velo-mini__img {
    width: 255px;
  }

  .velo-mini__text {
    font-size: 20px;
  }

  .velo-mini__medium-title {
    width: 320px;
  }

  .velo-mini__berry-title {
    width: 320px;
  }
}

@media (max-width: 1050px) {
  .velo-mini__text {
    transform: translate(0, 0);
  }

  .velo-mini {
    padding-left: 30px;
    padding-right: 30px;
  }

  .velo-mini-title {
    margin-right: 80px;
  }

  .velo-mint__triangle {
    left: 0;
  }

  .velo-mini-background {
    width: 59%;
  }

  .velo-mint__small-jar {
    left: 25%;
    transform: rotate(360deg);
  }

  .velo-mint__big-jar {
    left: 70px;
    top: -65px;
    z-index: 0;
  }

  .velo-jar-background {
    display: none;
  }
}

@media (min-width: 320px) and (max-width: 576px) {
  .velo-mini {
    padding-left: 15px;
    padding-right: 15px;
  }

  .velo-mini-title {
    color: #10145B;
    width: 328px;
    padding-top: 35px;
  }

  .velo-mini__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
    color: #000000;
    margin-top: 12px;
    width: 283px;

    strong {
      text-transform: uppercase;
      font-weight: 900;
    }
  }

  .velo-mini__medium {
    background: url('../images/veloNew/velo-mini-medium-bg.png') no-repeat center top / cover;
    width: 100%;
    height: 428px;
    position: relative;
  }

  .velo-mini__medium_content {
    position: absolute;
    bottom: 0;
    z-index: 2;
    padding-left: 15px;
  }


  .velo-mini__medium-title {
    color: #2cd5c4;
  }

  .velo-mini__medium_text {
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
    color: #fff;
    margin-top: 12px;
  }

  .velo-mini-berry {
    background-color: #10145b;
    width: 100%;
    height: 428px;
    position: relative;
    margin-top: -2px;
  }

  .velo-mini__berry_content {
    background: url('../images/veloNew/velo-mini-berry-bg.png') no-repeat center top / cover;
    height: inherit;
    width: 100%;
  }

  .velo-mini__berry_info {
    position: absolute;
    bottom: 25px;
    z-index: 2;
    padding-left: 15px;
  }

  .velo-mini__berry-title {
    color: #A5b0e3;
  }

  .velo-mini__img {
    display: none;
  }
}