#explanation-block {
  padding-top: 50px;
}

.explanation-item {
  background: #ededed;
  padding: 23px 0;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
  margin-bottom: 4px;
}

.explanation-item__container {
  font-weight: 900;
  font-size: 16px;
  line-height: 19px;
  color: #000;
  max-width: 1250px;
  margin: 0 auto;
}

@media (max-width: 992px) {
  .explanation-item__container {
    width: 95%;
  }
}
