.filter-btn__block {
  margin-top: 50px;
}

.filter-btn {
  width: 204px;
  padding: 17px 25px;
  background: #009681;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  border: none;
  text-decoration: none;
}

.filter-btn:last-child {
  background: #10145b;
  margin-left: 17px;
}

@media (min-width: 576px) {
  #filter-block {
    padding-top: 0;
    background: url('../images/veloNew/filterVector.png') no-repeat left center;
    background-color: #fff;
    position: relative;
    height: 80vh;
  }

  .filter-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-top: 30%;
    
  }

  .filter-btn {
    padding: 17px 70px;
  }
}

.filter-title {
  max-width: 575px;
  color: #10145b;

  span {
    color: #009681;
  }
}

@media (max-width: 992px) {
  #filter-block {
    padding-top: 20px;
  }

  .filter-btn__block {
    display: flex;
    flex-direction: column;
    align-items: center;

    a {
      width: 100%;
      margin-bottom: 30px;
    }
  }
}

@media (max-width: 576px) {
  .filter-title span {
    display: block;
  }
}

@media (max-width: 460px) {
  .filter-btn__block {
    flex-direction: column;
  }

  .filter-btn:last-child {
    margin-left: 0;
  }
}

@media (max-height: 680px) and (min-width: 991px) {
  #filter-block {
    background: url(../images/veloNew/filterVector.png) no-repeat -380px center;
    height: 80vh;

    .container {
      height: inherit;

      .filter-block {
        height: inherit;
        padding-top: 0;
        align-items: center;
      }
    }
  }

  .filter-title {
    margin-left: -145px;
  }
}