#sashe {
  padding-top: 40px;
}

.sashe-slider {
  margin-top: 40px;
}

.sashe-slide > .velo-good__title {
  text-align: left;
}

.sashe-slide > .velo-good__taste {
  text-align: left;
}

.sashe__title {
  font-weight: 900;
  font-size: 18px;
  line-height: 22px;
  color: #330072;
  margin-top: 15px;
  padding-left: 10px;
}

.sashe__text {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #330072;
  margin-top: 8px;
  padding-left: 10px;
}



@media (max-width: 576px) {
  .swiper-slide > .sashe__title {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide-active > .sashe__title {
    opacity: 1;
    transition: 0.5s;
  }

  .swiper-slide > .sashe__text {
    opacity: 0;
    transition: 0.5s;
  }

  .swiper-slide-active > .sashe__text {
    opacity: 1;
    transition: 0.5s;
  }
}

@media (min-width: 576px) {
  .init-sashe {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
  }

  .init-sashe > .swiper-slide {
    height: initial;
    width: initial;
  }
}


