@media (min-width: 576px) {
  #mini-adv {
    background-color: #009681;
    padding-bottom: 50px;

    .mini-adv-vector {
      background: url("../images/veloNew/adv-vector.png") no-repeat left center;
    }
  }

  .mini-adv__title {
    color: #2cd5c4;
    padding-top: 80px;

    span {
      display: block;
      color: #10145b;
    }
  }

  .mini-adv__img {
    display: flex;
    align-items: center;
  }

  .slider-number {
    font-weight: 900;
    font-size: 200px;
    line-height: 48px;
    letter-spacing: -1px;
    text-transform: uppercase;
    color: rgba(255, 255, 255, 0.5);
    margin-bottom: -140px;
  }

  .mini-adv__fix-width {
    width: 100%;
  }

  .mini-adv-slide__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
    color: #fff;
    width: 360px;
    margin-top: 35px;
  }

  .mini-adv__img {
    height: 270px;
    position: relative;
  }

  .mini-adv-slide__text {
    font-weight: 500;
    font-size: 23px;
    line-height: 28px;
    color: #fff;
    margin-top: 70px;
    width: 285px;
  }

  .mini-adv-slide__text-desktop {
    margin-top: 32px;
  }

  .mobile-img {
    display: none;
  }

  .velo-mini-big {
    position: absolute;
    bottom: 0;
  }

  .velo-mini-small {
    position: absolute;
    bottom: 0;
  }

  .velo-mini__shadow-jar {
    position: absolute;
    bottom: -38px;
  }

  .velo-mini__jar {
    position: absolute;
    bottom: 0;
  }

  .velo-mini__jar1 {
    position: absolute;
    bottom: -51px;
  }

  .velo-mini__jar2 {
    position: absolute;
    bottom: -51px;
  }

  .mini-adv-slide__text span {
    display: none;
  }

  .mini-adv-slide__text-mobile {
    display: none;
  }

  .mini-marge {
    margin-left: -35px;
  }
}

@media (max-width: 1175px) {
  .slider-number {
    display: none;
  }

  .desktop-img__block {
    display: none;
  }

  .mobile-img {
    display: inline;
  }

  .velo-mini__arrow {
    position: absolute;
    top: 30%;
    z-index: 3;
  }

  .mini-adv__slider {
    padding: 0 50px;
    overflow-x: hidden;
  }

  .arrow-prev {
    left: 0;
  }

  .arrow-next {
    right: 0;
  }
}

@media (max-width: 825px) {
  .mini-adv-slide__title {
    font-size: 24px;
  }

  .mini-adv-slide__text {
    font-size: 18px;
    width: initial;
  }
}

@media (min-width: 320px) and (max-width: 576px) {
  #mini-adv {
    background-color: #009681;


    .mini-adv-vector {
      background: url("../images/veloNew/vector-mini.png") no-repeat center center / cover;
      width: 100%;
      height: 90%;
      padding-top: 40px;
      padding-bottom: 20px;
    }
  }

  .mini-adv-slide__text-desktop {
    display: none;
  }

  .mini-adv-slide__text-mobile {
    display: block;
  }

  .mini-adv-slide__text span {
    display: inline;
  }

  .desktop-img__block {
    display: none;
  }

  .mobile-img {
    display: inline;
  }

  .mini-adv__title {
    color: #2cd5c4;
    margin-bottom: 45px;

    span {
      display: block;
      color: #10145b;
    }
  }

  .mini-adv__slider {
    position: relative;
  }

  .mini-adv__img {
    text-align: center;
  }

  .mini-adv-slide__title {
    font-weight: 400;
    font-size: 30px;
    line-height: 30px;
    color: #fff;
    width: 310px;
    margin-top: 30px;
  }

  .mini-adv-slide__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
    color: #fff;
    margin-top: 14px;
    width: 328px;
  }

  .velo-mini__arrow {
    position: absolute;
    top: 60px;
    z-index: 3;
  }

  .arrow-prev {
    left: 3%;
  }

  .arrow-next {
    right: 3%;
  }

  .mini-adv__slider {
    padding: 0;
  }
}
