#main-bg {
  width: 100%;
}

.top-block {
  background: url('../images/veloNew/desk_bg.png') no-repeat center top / cover;
  height: calc(100vh - 200px);
  overflow: hidden;
  position: relative;
}

.mobile-bg {
  display: none;
}

.big-jar {
  position: absolute;
  bottom: 100px;
  left: -20%;
  opacity: 0;
  transition: 2s;
}

.big-jar img {
  transform: rotate(180deg);
  transition: 2s;
}

.big-jar.active {
  opacity: 1;
  left: 30%;
}

.big-jar.active img {
  transform: rotate(360deg);
}

.small-jar {
  position: absolute;
  bottom: 90px;
  left: 10%;
  opacity: 0;
  transition: 2s;
}

.small-jar img {
  transform: rotate(180deg);
  transition: 2s;
}

.small-jar.active {
  opacity: 1;
  left: 41%;
}

.small-jar.active img {
  transform: rotate(360deg);
}

.small-jar-shadow {
  position: absolute;
  bottom: 90px;
  left: 5%;
  opacity: 0;
  transition: 2s;
}

.small-jar-shadow.active {
  opacity: 1;
  left: 41%;
}

.velo-mini-group {
  position: absolute;
  bottom: 71px;
  left: -40%;
  transition: 1.3s;
}

.velo-mini-group.active {
  left: 26%;
}

.first-screen-title {
  font-style: normal;
  font-weight: 700;
  font-size: 63px;
  line-height: 76px;
  text-transform: uppercase;
  color: #009681;
  position: absolute;
  bottom: 180px;
  opacity: 0;
  right: 80%;
  transition: 2s;
}

.first-screen-title.active {
  right: 75px;
  opacity: 1;
} 

.first-screen-subtitle {
  font-style: normal;
  font-weight: 700;
  font-size: 56px;
  line-height: 67px;
  text-transform: uppercase;
  color: #fff;
  position: absolute;
  bottom: 100px;
  right: -30%;
  opacity: 0;
  transition: 2s;
}

.first-screen-subtitle.active {
  right: 75px;
  opacity: 1;
  right: 60px;
} 

@media (max-width: 1800px) {
  .first-screen-title {
    font-size: 45px;
    line-height: 48px;
    bottom: 160px;
  }

  .first-screen-subtitle {
    font-size: 41px;
    bottom: 80px;
  }
}

@media (max-width: 1600px) {
  .big-jar img {
    width: 70%;
  }

  .small-jar-shadow img {
    width: 70%;
  }

  .small-jar img {
    width: 70%;
  }

  .velo-mini-group img {
    width: 70%;
  }
}

@media (max-width: 1350px) {
  .first-screen-title {
    font-size: 30px;
    line-height: 48px;
    bottom: 160px;
  }

  .first-screen-subtitle {
    font-size: 27px;
    right: 65px;
  }

  .velo-mini-group img {
    width: 64%;
  }
}

@media (max-width: 1080px) {
  .big-jar img {
    width: 55%;
  }

  .small-jar-shadow img {
    width: 55%;
  }

  .small-jar img {
    width: 55%;
  }

  .velo-mini-group img {
    width: 55%;
  }

  .first-screen-title {
    font-size: 24px;
    line-height: 28px;
  }
  .first-screen-subtitle {
    font-size: 22px;
    right: 65px;
  }

  .big-jar {
    opacity: 1;
    left: 5%;
    transform: rotate(360deg);
  }
  .small-jar {
    opacity: 1;
    left: 14%;
    transform: rotate(360deg);
  }
  .small-jar-shadow {
    opacity: 1;
    left: 14%;
  }
  .velo-mini-group {
    left: 0;
  }
  .first-screen-title {
    right: 75px;
    opacity: 1;
  } 
  .first-screen-subtitle {
    right: 75px;
    opacity: 1;
    right: 60px;
  } 

  .velo-mini-group img {
    width: 45%;
  }
}

@media (max-width: 576px) {
  .top-block {
    display: none;
  }

  .mobile-bg {
    display: block;
    width: 100%;
  }
}

@media (max-height: 680px) and (min-width: 991px) {
  .top-block {
    height: calc(100vh - 140px);
  }

  .first-screen-subtitle {
    right: -7%;
    transition: 1.5s;
  }
}
