@media (min-width: 576px) {
  #use-velo {
    padding-top: 80px;
    background: #10145b;
    margin-top: 80px;
    padding-bottom: 100px;
  }

  .use-velo__title {
    color: #fff;
    display: block;
    color: #2cd5c4;
    padding-left: 15px;
    font-size: 48px;
    line-height: 48px;
    margin-bottom: 54px;
  
    span {
      color: #a5b0e3;
    }
  }

  .use-velo__img-wrapper {
    text-align: center;
  }

  .use-velo__img-wrapper img {
    width: 75%;
  }

  .use-velo__list-description {
    font-weight: 400;
    font-size: 23px;
    line-height: 28px;
    text-align: center;
    color: #2cd5c4;
    margin-top: 28px;
  }

  .use-velo__list-item_number {
    font-weight: 700;
    font-size: 30px;
    line-height: 36px;
    color: #fff;
  }

  .use-velo__list {
    opacity: 0;
    transition: 1s;
    transform: translate(0, 500px);
  }
}

@media (max-width: 1150px) {
  .use-velo__list {
    opacity: 1;
  }

  .use-velo__list-description {
    font-size: 21px;
    line-height: 16px;
  }
}


@media (min-width: 320px) and (max-width: 576px) {
  #use-velo {
    padding-top: 40px;
    background: #10145b;
  }
  
  .use-velo__title {
    color: #fff;
    max-width: 310px;
    display: block;
    color: #2cd5c4;
    padding-left: 15px;
  
    span {
      color: #a5b0e3;
    }
  }
  
  .use-velo__list {
    display: flex;
    align-items: flex-start;
    justify-content: space-around;
    margin-top: 50px;
  }
  
  .use-velo__list-item {
    list-style: decimal;
    font-weight: normal;
    font-size: 40px;
    line-height: 48px;
    color: #fff;
    display: flex;
    flex-direction: column;
  }
  
  // .use-velo__list-item::before {
  //   content: counter(my-counter);
  //   counter-increment: my-counter;
  // }
  
  .use-velo__img-wrapper {
    text-align: center;
  }
  
  .use-velo__list-description {
    font-weight: 400;
    font-size: 23px;
    line-height: 28px;
    text-align: center;
    color: #2cd5c4;
    margin-top: 28px;
  }
  
  @media (max-width: 992px) {
    .use-velo__list {
      flex-wrap: wrap;
      justify-content: space-between;
    }
  
    .use-velo__list-item {
      margin-bottom: 50px;
    }
  }
  
  @media (max-width: 576px) {
    .use-velo__list {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  
    .use-velo__list-description {
      font-size: 14px;
      line-height: 20px;
    }
  
    .use-velo__list-item_number {
      font-size: 24px;
    }
  
    .swiper-slide > .use-velo__list-description {
      opacity: 0;
      transition: 0.5s;
    }
  
    .swiper-slide > .use-velo__list-item_number {
      opacity: 0;
      transition: 0.5s;
    }
  
    .swiper-slide-active > .use-velo__list-description {
      opacity: 1;
    }
  
    .swiper-slide-active > .use-velo__list-item_number {
      opacity: 1;
    }
  }
}


