.footer {
  background: #10145b;
  position: relative;
  padding-bottom: 90px;
  padding-top: 64px;
  // margin-bottom: 67px;
}

.footer-menu {
  display: flex;
  flex-direction: column;
}

.footer-link {
  text-decoration: none;
  font-style: normal;
  font-weight: bold;
  font-size: 19px;
  line-height: 23px;
  color: #fff;
}

.footer-content {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-logo {
  margin-right: 280px;
}

.footer-copyright {
  position: absolute;
  bottom: 20px;
  font-weight: bold;
  font-size: 18px;
  line-height: 18px;
  color: #fff;
  max-width: 580px;
  padding-left: 70px;
}

@media (max-width: 992px) {
  .footer-link {
    font-size: 16px;
  }

  .footer-copyright {
    padding-left: 20px;
    max-width: 400px;
  }
}

@media (max-width: 768px) {
  .footer-menu {
    max-width: 300px;
  }
}

@media (min-width: 320px) and (max-width: 576px) {
  .footer-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 0;
    margin-right: 0;
  }

  .footer-menu {
    margin-top: 42px;
  }

  .footer-link {
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    text-transform: uppercase;
  }

  .footer-content {
    flex-direction: column;
  }
}
