#pre-header {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #041e42;
  color: #fff;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  padding: 7px 0;
}

@media (max-width: 450px) {
  #pre-header {
    text-align: center;
  }
}