header {
  padding: 30px 60px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar a {
  text-transform: uppercase;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.5px;
  color: #041e42;
  text-decoration: none;
  padding: 20px 53px;
}

.header-btn {
  display: flex;
  align-items: center;
}

.header-btn a {
  display: flex;
  align-items: center;
  justify-content: center;
  width: inherit;
  height: inherit;
}

.btn-link:first-child {
  display: none;
}

.navbar a:hover {
  color: #00adbb;
}

.header-btns__block {
  display: flex;
  align-items: center;
}

.burger {
  display: none;
  width: 32px;
  height: 32px;
  margin-left: 15px;
}

.burger.active::before {
  content: '';
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #e5e5e5;
  opacity: 0.5;
  z-index: 3;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.header-icon {
  margin-right: 50px;
}

.icon-line {
  border: 1px solid rgba(156, 165, 168, 0.611915);
  height: 19px;
  width: 1px;
  margin-left: 5px;
  margin-right: 9px;
}

.social-link {
  margin-left: 26px;
}

@media (max-width: 1700px) {
  .navbar a {
    padding: 20px;
  }
}

@media (max-width: 1450px) {
  .header-icon {
    margin-right: 24px;
  }

  header {
    padding: 40px 20px;
  }
}

@media (max-width: 1300px) {
  .social-link {
    margin-left: 10px;
  }

  .header-icon {
    margin-right: 10px;
  }

  .navbar a {
    font-size: 16px;
  }

  .icon-line {
    margin: 0;
  }
}

@media (max-width: 992px) {
  .navbar {
    display: none;
  }

  .burger {
    display: flex;
  }

  .navbar a {
    padding: 20px;
  }

  .header-icon img {
    display: none;
  }

  .social-link img {
    display: none;
  }

  .icon-line {
    display: none;
  }

  .btn-link:first-child {
    display: flex;
  }

  .header-icon {
    margin-right: 0;
  }

  .header-btn {
    display: block;
  }
}

@media (min-width: 320px) and (max-width: 576px) {
  header {
    padding: 30px 20px;
  }

  .header-logo__link img {
    width: 70%;
  }
}

@media (max-height: 680px) and (min-width: 991px) {
  header {
    padding: 20px;
  }
}
